*{
	box-sizing: border-box;
	margin:0;
}

:root { 
--blue:#496D89; 
--yellow:#ffcc00;
--red:#aa5939;
}

body {
  max-width:1280px;
  min-width: fit-content;
  font-family: sans-serif;
  text-align: center;
  align-items: center;
}

button {
	color: var(--blue);
	position: relative;
	overflow: hidden;
	z-index: 1;
	transition: color 100ms ease-in-out;
	appearance: none;
	background-color: transparent;
	border: 2px solid var(--blue);
	cursor: pointer;
	font-size: 1rem;
	font-weight: 700;
	line-height: 1;
	padding: 1rem 2rem;
	text-decoration: none;
	text-align: center;
	text-transform: uppercase;
	font-family: 'Montserrat', sans-serif;}
	
button::after {
	  content: '';
	  position: absolute;
	  display: block;
	  top: 0;
	  left: 50%;
	  transform: translateX(-50%);
	  width: 0;
	  height: 100%;
	  background: var(--blue);
	  z-index: -1;
	  transition: width 150ms ease-in-out;
	}
	
button:hover {
	  color: #fff;}
	  
button:hover::after {
		width: 110%;
	}
   

a { text-decoration:none; 
	color: inherit;}

/* Nav Bar  */
.navbar {
 max-width : 1280px; 
position:fixed;
display:flex;   
background-color: rgba(0,0,0,0.7);
color:white; 
top: 0;
z-index: 100;
font-size: 1.2rem;
width:100%;
flex: 1; 
}

nav ul {
display: flex;
list-style-type: none;
flex:auto;
}

nav ul li {
flex: auto; }

nav li {
  padding: 0.3rem 0rem; 
  color:white;
	}

li:hover, .is-active{
    padding: 0.3rem 0rem; 
	background-color:white;
    color:black;
    }


/* Banner */

.bannertext { 
  font-size: 3rem; 
  position: absolute; 
          top: 50%;
          left: 50%;
          transform: translate(-50%,-50%);
